<template>
  <div>
    <el-dialog :visible.sync="editIconDialogVisibility" v-if="icon">
      <div slot="title" style="color: white; text-align: center">
        Edit Icon
      </div>
      <div class="input-group">
        <span class="label">Application Name</span>
        <el-input v-model="icon.name"></el-input>
      </div>
      <div class="input-group">
        <span class="label">Status</span>
        <el-select v-model="icon.status" style="width: 100%;">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>
      <div class="input-group">
        <span class="label">File Path</span>
        <el-input v-model="icon.path"></el-input>
      </div>

      <div class="input-group">
        <input
          type="file"
          style="display: none"
          ref="file"
          v-if="!selectedFile"
          @change="onFileChange"
        />
        <div class="label">Select Image</div>
        <el-button
          style="width: 150px; background: #DEDEDE; border-color: #404040"
          @click="$refs.file.click()"
          >Select</el-button
        >
      </div>

      <div class="action-buttons">
        <el-button class="btn-create" @click="onClickUpdate">Update</el-button>
        <el-button class="btn-delete" @click="openDeleteIconDialog"
          >Delete</el-button
        >
        <el-button class="btn-cancel" @click="editIconDialogVisibility = false"
          >Cancel</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  computed: {
    editIconDialogVisibility: {
      get() {
        return this.$store.state.editIconDialogVisibility;
      },
      set(v) {
        this.$store.commit("setEditIconDialogVisibility", v);
      },
    },
    ...mapState("icon", ["updateIcon"]),
  },
  data() {
    return {
      icon: null,
      options: [
        {
          value: true,
          label: "Active",
        },
        {
          value: false,
          label: "Deactivate",
        },
      ],
      selectedFile: null,
    };
  },
  methods: {
    openDeleteIconDialog() {
      this.$store.commit("setEditIconDialogVisibility", false);
      this.$store.commit("setDeleteIconDialogVisibility", true);
    },
    onClickUpdate() {
      const formData = new FormData();
      formData.append("file", this.selectedFile);
      formData.append("name", this.icon.name);
      formData.append("path", this.icon.path);
      formData.append("status", this.icon.status);

      this.$store.dispatch("icon/update", {
        icon: formData,
        notify: this.$notify,
        id: this.icon._id,
      });
      this.selectedFile = null;
      this.editIconDialogVisibility = false;
    },
    onClickCancel() {
      this.editIconDialogVisibility = false;
    },
    onFileChange(e) {
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      this.selectedFile = files[0];
    },
  },
  watch: {
    editIconDialogVisibility() {
      this.icon = { ...this.updateIcon };
    },
  },
};
</script>

<style lang="scss" scoped>
@import "src/assets/scss/new-user-modal.scss";
@import "src/assets/scss/background-modal.scss";
</style>
