<template>
  <div>
    <el-dialog :visible.sync="computerDialog">
      <div slot="title" style="color: white; text-align: center">
        Assign Computers
      </div>
      <div style="display: flex; justify-content: space-around;">
        <div>
          <div class="subtitle">Active Computers</div>
          <div class="list-area">
            <el-checkbox-group
              v-model="activeCheckList"
              style="display: flex; flex-direction: column"
            >
              <el-checkbox
                v-for="(computer, index) in activeComputers"
                :key="`active-computer-${index}`"
                :label="computer._id"
              >
                {{ computer.computerName }}
              </el-checkbox>
            </el-checkbox-group>
          </div>
        </div>
        <div
          style="display: flex; flex-direction:column; justify-content: center;"
        >
          <img
            class="arrow-image"
            src="@/assets/image/swap_right.png"
            @click="moveToAvailableComputers"
          />
          <img
            class="arrow-image"
            style="margin-top: 10px;"
            src="@/assets/image/swap_left.png"
            @click="moveToActiveComputers"
          />
        </div>
        <div>
          <div class="subtitle">Available Computers</div>

          <div class="list-area">
            <el-checkbox-group
              v-model="availableCheckList"
              style="display: flex; flex-direction: column"
            >
              <el-checkbox
                v-for="(computer, index) in availableComputers"
                :key="`available-computer-${index}`"
                :label="computer._id"
              >
                {{ computer.computerName }}
              </el-checkbox>
            </el-checkbox-group>
          </div>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <div class="ngc-button btn-save" @click="updateActiveComputers">
          Save
        </div>
        <div class="ngc-button btn-cancel" @click="computerDialog = false">
          Cancel
        </div>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  computed: {
    computerDialog: {
      get() {
        return this.$store.state.computerDialogVisibility;
      },
      set(v) {
        this.$store.commit("setComputerDialogVisibility", v);
      },
    },
    computerList() {
      return this.$store.state.computer.list;
    },
    storeActiveComputers() {
      return this.$store.state.group.activeComputers;
    },
  },
  data() {
    return {
      availableComputers: [],
      activeComputers: [],
      availableCheckList: [],
      activeCheckList: [],
    };
  },
  methods: {
    moveToActiveComputers() {
      let newAvailableComputers = this.availableComputers.filter((computer) => {
        if (this.availableCheckList.indexOf(computer._id) > -1) {
          this.activeComputers.push(computer);
          return false;
        }
        return true;
      });

      this.availableComputers = newAvailableComputers;
      this.availableCheckList = [];
    },
    moveToAvailableComputers() {
      let newActiveComputers = this.activeComputers.filter((computer) => {
        if (this.activeCheckList.indexOf(computer._id) > -1) {
          this.availableComputers.push(computer);
          return false;
        }
        return true;
      });

      this.activeComputers = newActiveComputers;
      this.activeCheckList = [];
    },
    updateActiveComputers() {
      const activeList = this.activeComputers.map((computer) => computer._id);
      this.$store.commit("group/setActiveComputers", activeList);
      this.computerDialog = false;
    },
    checkActiveComputers() {
      let availableComputers = [];
      let activeComputers = [];
      this.computerList.forEach((computer) => {
        if (
          this.storeActiveComputers.findIndex((id) => id == computer._id) > -1
        ) {
          activeComputers.push(computer);
        } else {
          availableComputers.push(computer);
        }
      });

      this.activeComputers = activeComputers;
      this.availableComputers = availableComputers;
    },
  },
  mounted() {
    this.$store.dispatch("computer/loadList", { notify: this.$notify });
  },
  watch: {
    computerList() {
      // this.availableComputers = this.computerList;
      this.checkActiveComputers();
    },
    storeActiveComputers() {
      this.checkActiveComputers();
    },
  },
};
</script>

<style lang="scss" scoped>
@import "src/assets/scss/computers-modal.scss";
@import "src/assets/scss/new-group.scss";
</style>
