<template>
  <div>
    <el-dialog :visible.sync="computerDialog">
      <div slot="title" style="color: white; text-align: center">
        Background
      </div>
      <input
        type="file"
        @change="onFileChange"
        style="display: none"
        ref="file"
      />
      <div class="image-preview">
        <img :src="image" alt="" />
      </div>
      <div class="select-image">
        <span class="sl-label">Select Image</span>
        <div class="sl-button" @click="$refs.file.click()">Select</div>
      </div>

      <div class="action-buttons">
        <div class="ngc-button btn-save" @click="uploadBackground">
          Updated
        </div>
        <div class="ngc-button btn-cancel" @click="computerDialog = false">
          Cancel
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import { SERVER_URL } from "../config";
export default {
  computed: {
    computerDialog: {
      get() {
        return this.$store.state.backgroundDialogVisibility;
      },
      set(v) {
        this.$store.commit("setBackgroundDialogVisibility", v);
      },
    },
    backgroundImage() {
      return this.$store.state.group.backgroundImage;
    },
  },
  data() {
    return {
      image: "",
      file: null,
    };
  },
  methods: {
    onFileChange(e) {
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      this.file = files[0];
      this.createImage(files[0]);
    },
    createImage(file) {
      var reader = new FileReader();
      var vm = this;

      reader.onload = (e) => {
        vm.image = e.target.result;
      };
      reader.readAsDataURL(file);
    },
    uploadBackground() {
      const formData = new FormData();
      formData.append("file", this.file);
      axios
        .post(SERVER_URL + "/groups/uploadBackground", formData)
        .then((res) => {
          this.$store.commit("group/setBackgroundImage", res.data);
          this.$notify({
            title: "Success",
            message: "Background image uploaded!",
            type: "success",
          });
        })
        .catch((err) => {
          this.$notify.error({
            title: "Failed to upload background",
            message: err.message,
          });
        });
      this.computerDialog = false;
    },
  },
  watch: {
    backgroundImage() {
      if (this.backgroundImage.length > 0) {
        this.image = this.$options.filters.filePath(this.backgroundImage);
      }
    },
  },
};
</script>

<style lang="scss" scoped>
@import "src/assets/scss/background-modal.scss";
@import "src/assets/scss/new-group.scss";
</style>
